.users-content {
  margin: 0 1rem 0 1rem;
}



.form-control {
  margin-left: 1rem;
  margin-top: 1rem;
  border: 1px solid #1c204c;
  color: var(--color);
  padding: calc(1.5 * var(--space)) calc(1 * var(--space));
  display: block;
  outline: 0;
  background: #1c204c;
  box-shadow: 0 2px 4px var(--shadow);
  border-radius: 3px;
	margin: 0;
}

.search-user {
  width: 97%;
}
.MuiSvgIcon-root {
  fill: currentColor;
  width: 0.7em;
  height: 0.7em;
  display: inline-block;
  font-size: 1.5rem;
  transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  flex-shrink: 0;
  user-select: none;
}

.actions-adm{
	display: flex;
	width: 100%;
}
.actions-adm .users-item__actions{
	width: 100%;
}
.actions-adm .users-item__actions a{
	width: 100%;
}
.actions-adm .users-item__actions a .act{
	width: 100%;
}
.actions-adm .act{
	width: 100%;
}